.App {
  margin: 0 auto; }

.App-logo {
  position: absolute;
  top: 0;
  right: 30px;
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 45px;
  padding: 5px; }

.button-logo {
  position: absolute;
  top: -10px;
  height: 60px;
  width: 60px;
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear; }

.fcc-logo {
  position: absolute;
  top: 0;
  left: 10px;
  height: 45px;
  padding: 5px; }

.App-header {
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  background-color: darkgreen;
  height: 50px;
  padding: 5px; }

.footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 45px;
  width: 100%;
  background-color: darkgreen;
  color: #fff; }

.author {
  position: absolute;
  padding: 0 0 5px 5px; }

.links {
  position: absolute;
  padding: 0 10px 0 0;
  right: 10px;
  cursor: pointer; }

.menu-card {
  height: 450px;
  background-color: teal;
  color: white; }

.menu-list {
  cursor: pointer;
  padding: 0; }

.menu-list:hover {
  font-weight: 900;
  background-color: seagreen; }

.mdc-card {
  position: absolute;
  margin: 50px auto;
  left: 40%;
  text-align: left;
  backdround-color: teal;
  height: 450px;
  width: 350px;
  color: white; }

.recipe-box {
  height: 450px;
  width: 350px; }

.card-title {
  background-color: seagreen;
  height: 30px;
  color: lightgreen;
  font-size: 1.2em;
  font-weight: 900; }

.card-actions {
  background-color: seagreen;
  height: 70px; }

.card-button1 {
  position: absolute;
  right: 5px;
  padding-right: 10px;
  padding-left: 10px; }

.card-button2 {
  position: absolute;
  right: 55px;
  padding-right: 10px;
  padding-left: 10px; }

.card-button3 {
  position: absolute;
  right: 105px;
  padding-right: 10px;
  padding-left: 10px; }

.card-button4 {
  position: absolute;
  right: 5px;
  padding-right: 10px;
  padding-left: 10px; }

label[for=menu-toggle] {
  cursor: pointer; }

#menu-toggle {
  position: absolute;
  right: 5px;
  bottom: 7px;
  height: 35px;
  width: 35px;
  display: none; }

#menu-toggle:not(:checked) ~ #menu-buttons {
  display: none; }

.menu ul {
  margin: 0;
  padding: 0;
  list-style-type: none; }

.menu ul li {
  display: inline; }

.ingredients, .directions {
  position: relative;
  display: block;
  margin: 0;
  padding: 0;
  line-height: 5px; }

.ingredients, .directions p {
  position: relative;
  left: -25px;
  margin-top: -8px;
  line-height: 0.15;
  line-height: 100%;
  padding: 0; }

.back-button {
  position: absolute;
  bottom: 7px;
  cursor: pointer; }

#confirm {
  margin-left: 10px; }

.confirm li {
  position: relative;
  bottom: -30px;
  right: -50px;
  display: inline-block; }

.confirm-edit li {
  position: relative;
  bottom: 10px;
  left: -40px;
  margin-right: 10px;
  display: inline-block; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }



